<template>
    <el-menu
            class="el-menu-vertical"
            collapse-transition
            :collapse="$root.menuToggle"
            :default-active="activeIndex"
            text-color="#fff"
            background-color="#3c3c3c"
            active-text-color="#fff"
            router
    >
        <template v-for="(item,index) in router">
            <el-menu-item v-if="item.children.length === 1" :index="index.toString()" :route="item.children[0]"
                          :key="index">
                <i :class="iconPrefix+item.children[0].meta.icon"></i><span slot="title"
                                                                            v-text="item.children[0].meta.title"></span>
            </el-menu-item>

            <el-submenu v-else :index="index.toString()" :route="item" :key="index">
                <template slot="title">
                    <i :class="iconPrefix+item.meta.icon"></i>
                    <span v-text="item.meta.title"></span>
                </template>
                <el-menu-item-group>
                    <el-menu-item v-for="(children,childrenIndex) in item.children" :key="childrenIndex"
                                  :index="index+ '-' +childrenIndex" :route="children"
                                  v-text="children.meta.title"></el-menu-item>
                </el-menu-item-group>
            </el-submenu>
        </template>
    </el-menu>
</template>

<script type="text/ecmascript-6">
    export default {
        name: 'vAside',
        data() {
            return {
                iconPrefix: 'menuicon icon-'
            }
        },
        computed: {
            router() {
                return this.$store.state.user.asyncRouter
            },
            activeIndex() {
                return this.$route.meta.index;
            }
        }
    }
</script>

